body {
  font-family: 'Helvetica Neue', sans-serif;
  margin: 0;
  background: #fff;
  color: #222;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 1rem 2rem;
  border-bottom: 1px solid #eee;
  background: #fff;
  position: sticky;
  top: 0;
}

.nav-links a {
  margin-left: 1.5rem;
  text-decoration: none;
  color: #333;
  font-weight: 500;
}

.nav-logo {
  font-weight: bold;
  font-size: 1.2rem;
}

.home {
  text-align: center;
  padding: 4rem 2rem;
}

.gallery {
  padding: 2rem;
}

.grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.video-container iframe {
  width: 100%;
  aspect-ratio: 16 / 9;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
}
